import { Link, useNavigate } from 'react-router-dom';

const Buttons = (props) => {    
    // const navigate = useNavigate();

    // const navigateToOtpVerify = () => {
    //     navigate('/auth/login-otp-verify');
    // };

    return ( 
        <div className="form-control">
            <button className={props.className} disabled={props.disabled} type={props.type} onClick={props.buttonClickHandle}>
                {props.children}
            </button>
        </div>
     );
}
 
export default Buttons;