import React, { Component } from 'react';
import AppPreview from '../../../assets/images/auth/app-preview.svg';
import AppStore from '../../../assets/images/app-store.svg';
import PlayStore from '../../../assets/images/play-store.svg';

import './auth-app-preview.styles.scss';

const AuthAppPreview = () => {
    return ( 
        <div className="auth-app-preview d-flex d-flex-column align-items-center">
                <div className="app-preview">
                    <img src={AppPreview} alt='App Preview' />
                </div>

                <div className="download-veve-market-place text-center">
                    <p className='mb10'>Download VEVE Market app</p>

                    <div className="app-redirection">
                        <a href='#'>
                            <img src={AppStore} alt="App Store" />
                        </a>
                        <a href='#'>
                            <img src={PlayStore} alt="Play Store" />
                        </a>
                    </div>
                </div>
            </div>
     );
}

export default AuthAppPreview;