import React from 'react';
import { Route, Routes } from 'react-router-dom';

import './App.scss';
import Authentication from './components/authentication/authentication.component';
import Contact from './components/authentication/contact/contact.component';
import Home from './components/authentication/login/home';
import QuickStart from './components/authentication/quick-start/quick-start.component';
import MarketPlace from './components/marketplace/marketplace.component';

function App() {
  return (
    <div className="App">
      <Routes>
        <Route path='/' element={<MarketPlace />} />
        <Route path='/login' element={<Authentication />} />
        <Route path='/contact' element={<Contact />} />
        <Route path='/home' element={<Home />} />
        <Route path='/quick-start' element={<QuickStart />} />
      </Routes>
        {/* <Authentication></Authentication> */}
    </div>
  );
}

export default App;
