import React from 'react';
import FormInput from '../../form-components/form-input';
import Buttons from '../../form-components/buttons';
import Dropdown from '../../form-components/dropdown';

const ContactPublisher = (props) => {
    return ( 
        <React.Fragment>
            <FormInput label={'Name'} id={'name'} inputType={'text'} placeholder={'Enter Name'} />

            <FormInput label={'Email'} id={'email'} inputType={'email'} placeholder={'Enter Email'} />

            <FormInput label={'Phone Number'} id={'phone'} inputType={'number'} placeholder={'Phone Number'} phone={'phone-number'} checkIsPhone={true} />

            <Dropdown />

            <FormInput label={'Company Name'} id={'company'} inputType={'text'} placeholder={'Enter Company Name'} />

            <Buttons className={'btn btn-disabled'} disabled>Submit</Buttons>
        </React.Fragment>
     );
}
 
export default ContactPublisher;
