import React, { Component, useState } from 'react';
import FormInput from '../../form-components/form-input';
import Buttons from '../../form-components/buttons';
import { Link } from 'react-router-dom';

const LoginWithOTP = (props) => {     
    const [phone, setPhone] = useState('');
    const [isPhone, setIsPhone] = useState(false);
    
    const OnIsPhoneNumber = (e) => {    
        const value =   e.target.value;        
        if (isNaN(value) || (value == null || value == "")) {
            setPhone("");
            setIsPhone(false);
        }
        else {
            setPhone("phone-number");
            setIsPhone(true);
        }

        //isNaN(value) || value == null || value == "" ? (setPhone(""), setIsPhone(false)) : ( setPhone("phone-number"), setIsPhone(true));   
    }
    console.log("Phone", phone, "Isphone", isPhone);

    return ( 
        <React.Fragment>
            <form action="" id='sign_in_otp'>
                <FormInput label={'Enter email id or mobile number'} id={'emailORmobile'} inputType={'text'} placeholder={'Enter email id or mobile number'} handleIsPhoneNumber={OnIsPhoneNumber} phone={phone} checkIsPhone={isPhone} />

                <Buttons className={'btn btn-disabled'} disabled type={'button'} showOTP={props.showOTP} buttonClickHandle={props.onVerifyOtp}>Get OTP</Buttons>

                <div className="or-block mb20 mt20">OR</div>

                <div className='text-center'>
                    <Link onClick={props.handleLinkRedirect}>Enter username or password</Link>
                </div>
            </form>            
        </React.Fragment>
     );
}
 
export default LoginWithOTP;
