import React, { useState } from "react";
import { useNavigate, Link } from 'react-router-dom';

import Logo from "../../../assets/images/logo.svg";

const Home = () => {
  const [active, setActive] = useState("");  
  const navigate = useNavigate();
  
  const handleUserRedirect = (user) => {
    setActive(user);

    setTimeout(() => {
      navigate(`/quick-start?user=${user}`);      
    }, 500);
  }

  const UserType = (props) => {
    return (
      <div
        className={`choose-user text-center ${
          active == props.usertype ? "active" : ""
        }`}
        onClick={() => handleUserRedirect(props.usertype)}
      >
        <div className="choose-user-icon"></div>

        <h3>{props.usertype == 'publisher' ? 'I am a Publisher' : 'I represent a Brand'}</h3>

        <Link href='' className="btn btn-primary">Continue</Link>
      </div>
    );
  };

  return (
    <div className="choose-user-type d-flex d-flex-column d-justify-center align-items-center">
      <div className="logo">
        <img src={Logo} alt="" />
      </div>

      <h1>Choose the type of account you would like to proceed with</h1>

      <div className="choose-user-container d-flex d-justify-center">
        <UserType usertype={"publisher"} />

        <UserType usertype={"brand"} />
      </div>
    </div>
  );
};

export default Home;
