import React, { Component, useState } from "react";
import { Link } from "react-router-dom";
import Logo from "../../assets/images/logo.svg";
import AuthAppPreview from "./auth-app-preview/auth-app-preview.component";

import "./authentication.styles.scss";
import Footer from "../footer/footer.component";
import LoginWithOTP from "./login/login-with-otp.component";
import LoginWithUsername from "./login/login-with-username.component";
import VerifyOtp from "./login/verify-otp.component";
import SignUpBrand from "./contact/contact-brand.component";
import SignUpPublisher from "./contact/contact-publisher.component";
import ChooseUserType from "./login/home";


const Authentication = () => {
    const [loginType, setLoginType] = useState("otpLogin");
    
    return (
        // <ChooseUserType usertype={'usertype'} />
        <React.Fragment>
            <div className="auth-container d-flex d-justify-between">
                <div className="auth-components d-flex d-flex-column d-justify-center">
                    <div className="logo">
                    <img src={Logo} alt="" />
                    </div>
    
                    <div className="auth-login">
                    {/* <div className={`error-message text-center ${props.error_show}`}>{props.error}</div> */}
    
                    <h1>Welcome to VEVE Market</h1>
    
                    {
                        loginType == "otpLogin" ? 
                        <LoginWithOTP
                            onVerifyOtp={() => setLoginType("verifyOtp")}
                            handleLinkRedirect={() => setLoginType("usernameLogin")}
                        /> : 
                        loginType == "verifyOtp" ? <VerifyOtp /> : 
                        loginType == "usernameLogin" ? <LoginWithUsername handleLinkRedirect={() => setLoginType("otpLogin")} />  : ""
                    }
                    </div>
    
                    <div className="dont-have-account text-center">
                        <label>Don't have an account?</label>
                        <Link to={"/contact"}>
                            Contact us to create an account
                        </Link>
                    </div>
    
                    {/* <LoginWithOTP />
                                <LoginWithOTP error_show={'error-show'} error={'Error message goes here'} />
                                <LoginWithUsername />
                                <LoginWithUsername error_show={'error-show'} error={'Error message goes here'} />
                                */}
    
                    {/* <VerifyOtp />
                                <VerifyOtp resend_code={'resend-code'} /> */}
    
                    {/* <SignUpBrand /> */}
    
                    {/* <SignUpPublisher />                     */}
                </div>
    
                <AuthAppPreview></AuthAppPreview>    
            </div>
            <Footer></Footer>
        </React.Fragment>
  );
};

export default Authentication;
